//HEADER
@media screen and (max-width: 867px) {
  body.scrollLock {
    overflow: hidden;
  }
}

.navbar {
  box-shadow: 1px 1px 7px $brandGrey;
  background-color: white;
  position: fixed;
  width: 100%;
  z-index: 5;
  &__container {
    overflow: auto;
    max-width: 1180px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    &_logo {
      padding: 25px 0;
      @media only screen and (max-width: 700px) {
        padding: 15px 0;
      }
    }
    .navbar__items {
      &_item {
        @include textStyle;
        overflow: scroll;
        &:not(:first-child) {
          margin-left: 45px;
        }
      }
    }
  }
  .hiddenMenu {
    @include btn;
    margin: 0;
    display: none;
    padding-bottom: 0;
    //padding: 0;
    padding-top: 10px;
  }
  @media screen and (max-width: 867px) {
    .navbar__items {
      -moz-user-select: none;
      -khtml-user-select: none;
      user-select: none;
      display: none;
      overflow: auto;
      &_item {
        overflow: auto;
        @include textStyle;
        &:not(:first-child) {
          margin-left: 0;
          margin-top: 25px;
        }
      }
    }
    .hiddenMenu {
      display: flex;
      margin-left: auto;
      margin-bottom: 5px;
    }
    .clicked3 {
      padding-bottom: 200%;
      // animation: anim2 0.3s ease;
    }
    .clicked2 {
      display: flex;
      flex-direction: column;
      transition: all 0.2s ease;
      animation: anim2 0.3s ease;
      position: absolute;
      margin: 0 auto;
      top: 200px;
      left: 50%;
      transform: translate(-50%, -50%);
      &:before {
        display: none;
      }
      @keyframes anim2 {
        0% {
          // position: relative;
          top: 180px;
        }
        100% {
          //position: relative;
          top: 200px;
        }
      }
    }
  }
}

.logo {
  @media only screen and (max-width: 700px) {
    max-width: 100px;
  }
}
