.footer {
  box-shadow: 1px 1px 5px $brandGrey;
  padding-bottom: 30px;
  &__logo {
    display: flex;
    justify-content: center;

    img {
      margin: 50px auto 10px;
    }
  }

  .menu {
    max-width: 100%;
    &__nav {
      display: flex;
      justify-content: center;
      margin: 30px auto;
      &_item {
        color: $brandGrey;
        position: relative;
        text-align: center;
        font-weight: bold;
        &:hover {
          color: $brandBlack;
        }
        &:not(:first-child) {
          margin-left: 50px;
          @include textStyle2;

          &:before {
            width: 0;
            height: 0;
          }
        }
      }
    }
  }

  &__copyright {
    color: $brandGrey;
    @include row-flex();
    text-transform: uppercase;

    max-width: 630px;
    margin: 30px auto 0;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

    span {
      @include col();
      @include size-lg(4);
      @include size-md(4);
      @include size-xs(12);

      margin: 10px 0;

      &:first-child {
        max-width: 10%;

        @media screen and (max-width: 767px) {
          max-width: 20%;
        }
      }
    }

    .black {
      color: black;
    }
  }
}
