          font-weight: lighter;
          letter-spacing: 6px;
          text-align: center;
          @media screen and (max-width: 867px) {
            font-size: 14px;
          }
        }
      }
    }
    .slide2 {
      position: relative;
      .text2 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 6;
        .h1 {
          text-align: center;
          background-color: black;
          color: white;
          font-weight: lighter;
          font-size: 48px;
          padding: 0 45px;
          letter-spacing: 5.5px;
          margin-bottom: 35px;
          min-width: 300px;
          @media screen and (max-width: 867px) {
            font-size: 20px;
            padding: 0 0px;
            margin-bottom: 5px;
          }
        }
        .h2 {
          font-size: 18px;
          font-weight: lighter;
          letter-spacing: 6px;
          text-align: center;
          @media screen and (max-width: 867px) {
            font-size: 14px;
          }
        }
      }
    }
    .slide3 {
      position: relative;
      .text3 {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 6;
        .h1 {
          text-align: center;
          background-color: black;
          color: white;
          font-weight: lighter;
          font-size: 48px;
          padding: 0 45px;
          letter-spacing: 5.5px;
          margin-bottom: 35px;
          min-width: 300px;
          @media screen and (max-width: 867px) {
            font-size: 20px;
            padding: 0 0px;
            margin-bottom: 5px;
          }
        }
        .h2 {
          font-size: 18px;
          font-weight: lighter;
          letter-spacing: 6px;
          text-align: center;
          @media screen and (max-width: 867px) {
            font-size: 14px;
          }
        }
      }
    }
    img {
      max-width: 100%;
      margin: 0;
      //background-position: center;
      //background-size: contain;
    }
