}

.fa-angle-left:before,
.fa-angle-right:before {
  font-size: 35px;
  color: rgb(255, 255, 255);
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(100%, -50%);
  cursor: pointer;
  z-index: 2;
  border: 3px solid white;
  border-radius: 50%;
  transition: all 0.2s ease;
  opacity: 0.8;
}

.fa-angle-left:hover:before,
.fa-angle-right:hover:before {
  background-color: black;
  border-color: black;
}

.fa-angle-left:before {
  padding: 10px 25px 12px 20px;
}

.fa-angle-right:before {
  top: 50%;
  left: 100%;
  transform: translate(-199%, -50%);
  padding: 10px 20px 12px 25px;
}

//DOTS
.slick-dots {
  position: absolute;
  top: 80%;
  @media screen and (max-width: 575px) {
    top: 75%;
  }
  @media screen and (max-width: 500px) {
    display: none;
    width: 0;
    height: 0;
  }
  & li {
    margin: 10px 5px;
  }
  .fa-circle:before {
    color: white;
    font-size: 16px;
    @media screen and (max-width: 500px) {
      display: none;
    }
  }
  .fa-circle:hover:before {
    font-size: 7px;
    padding: 4px 3.5px 4px 4.4px;
    background-color: black;
    border-radius: 100%;
    position: relative;
    top: -3px;
  }
  & li.slick-active .fa-circle:before {
    color: black;
  }
}

@media screen and (max-width: 867px) {
  .fa-angle-left:before,
  .fa-angle-right:before {
    display: none;
  }
}

//MENU
.menu {
  max-width: 1140px;
  margin: 30px auto 0px;
  border-top: 1px solid $brandSilver;
  border-bottom: 1px solid $brandSilver;
  &__nav {
    display: flex;
    justify-content: center;
    margin: 20px auto;
    &_item {
      color: $brandGrey;
      position: relative;
      text-align: center;
      font-weight: bold;

      &:hover {
        color: $brandBlack;
      }
      &:not(:first-child) {
        margin-left: 50px;
        @include textStyle2;

        @media only screen and (max-width: ) {
        }
      }
    }
  }
  .bar {
    @include btn;
    max-width: 130px;
    display: none;
  }
  @media screen and (max-width: 700px) {
    &__nav {
      display: none;
      transition: all 0.2s ease;

      flex-direction: column;

      &:before {
        width: 0;
      }
      &_item {
        transition: all 0.2s ease;
        padding: 15px 0;
        &:hover {
          background-color: $brandSilver;
        }
        &:not(:first-child) {
          margin-left: 0 !important;
        }
      }
    }
    .bar {
      @include btn;
      transition: all 0.2s ease;
      margin: 20px auto;
    }
    .clicked {
      display: flex;
      flex-direction: column;
      transition: all 0.2s ease;
      animation: anim 0.3s ease;
      &:before {
        display: none;
      }
      @keyframes anim {
        0% {
          position: relative;
          top: -10px;
        }
        100% {
          position: relative;
          top: 0px;
        }
      }
    }
  }
}

.store {
  max-width: 1170px;
  margin: 0 auto;
  padding-bottom: 50px;
  &__items {
    @include row-flex();
    // @include col();
    display: flex;
    justify-content: center;
    margin: 20px auto 30px;
    flex-direction: row;
    .columnLeft,
    .columnCenter,
    .columnRight {
      @include col();
      @include size-lg(4);
      @include size-md(8);
      @include size-xs(10);
    }
    &_item {
      margin: 30px auto 0;
      height: auto;
      padding-bottom: 0;
      margin-bottom: 0;
      position: relative;
      cursor: pointer;
      display: flex;
      box-shadow: 0px 0px 10px 0px rgba(170, 170, 170, 0.644);
      transition: all 0.5s ease;
      &:hover {
        transform: scale(1.025) !important;
      }
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0);
        transition: all 0.3s ease;
      }
      &:hover:before {
        background-color: rgba(0, 0, 0, 0.192);
      }
      .img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        //margin-bottom: auto;
      }
    }
  }
  &__btn {
    @include btn;
  }
}

.advCont {
  @include row-flex();
  @include md(justify-content, center);
  max-width: 1140px;
  position: relative;
  margin: 30px auto;
  .advItem {
    @include col();
    @include size(4);
    @include size-md(4);
    @include size-xs(6);

    @media only screen and (max-width: 550px) {
      @include size(12);
    }
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 0px auto 30px;

    &__title {
      font-size: 18px;
      color: rgb(100, 100, 100);
      text-shadow: none !important;
      @media only screen and (max-width: 700px) {
        font-size: 15px;
      }
    }
  }
}

.fa {
  font-size: 35px;
  margin-bottom: 10px;
}

.advIcon {
  &::before {
    font-size: 40px;
    color: rgb(163, 163, 163);
    @media only screen and (max-width: 700px) {
      font-size: 30px;
    }
  }
}

.text {
  font-size: 18px;
  color: white;
  text-shadow: 0px 1px 1px black;
  text-align: center;
  color: rgb(112, 112, 112);
  max-width: 70%;
  margin: 0 auto;
  @media screen and (max-width: 400px) {
    font-size: 13.5px;
  }
  &-promo {
    font-size: 40px;
  }
  &-centred {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
  }
  &-black {
    color: #000;
  }
  &-bold {
    font-size: 20px !important;
  }
  &-heading {
    font-size: 80px;
    margin-bottom: 30px;
  }
  @media screen and (max-width: 800px) {
    max-width: 85%;
    font-size: 16px;
    &-promo {
      font-size: 30px;
    }
    &-heading {
      font-size: 55px;
    }
  }
  @media screen and (max-width: 550px) {
    font-size: 15px;
    &-promo {
      font-size: 25px;
    }
    &-heading {
      font-size: 44px;
    }
  }
  @media screen and (max-width: 400px) {
    &-promo {
      font-size: 20px;
    }
    &-heading {
      font-size: 35px;
    }
  }
}

.img-slide {
  width: 100%;
}

.slick-dotted {
  top: -14px;
}

