@mixin textStyle {
  color: $brandGrey;
  position: relative;
  text-align: center;
  font-weight: bold;

  &:hover {
    color: $brandBlack;

    &::before {
      content: "";
      display: inline-block;
      position: absolute;
      //padding: 20px 0;
      margin-top: 7px;

      left: -35px;
      width: 20px;
      height: 1px;
      //margin: 50px auto 0;
      border-radius: 50px;
      background: black;
      align-items: center;
    }
  }
}

@mixin textStyle2 {
  &:before {
    content: "";
    display: inline-block;
    position: absolute;
    //padding: 20px 0;
    margin-top: 7px;

    left: -40px;
    width: 30px;
    height: 1px;
    //margin: 50px auto 0;
    border-radius: 50px;
    background: $brandSilver;
    align-items: center;
    transform: rotate(-65deg);
  }
}

@mixin btn {
  color: white;
  position: relative;
  text-align: center;
  font-weight: bold;
  background-color: $brandGrey;
  padding: 8px 20px;
  border-radius: 3px;
  margin: 50px auto;
  text-align: center;
  display: flex;
  align-self: center;
  justify-content: center;
  max-width: 130px;
  transition: all 0.2s ease;
  cursor: pointer;

  &:hover {
    background-color: darken($color: $brandGrey, $amount: 8);
  }
}
